body,
html {
  background-color: white;
  -webkit-tap-highlight-color: transparent;
}

.main-container {
  letter-spacing: 0.2rem;
  color: yellow;
  text-shadow: -1px 0 black, 0 1px black, 1px 0 black, 0 -1px black;
  font-weight: bold;
  display: -webkit-flex;
  -webkit-align-items: center;
  flex-direction: row;
  justify-content: center;
  flex-wrap: wrap;
  height: 90vh;
}

